import React, { useEffect, useRef, useState } from 'react';
import pv from 'bio-pv';

const ProteinViewer = () => {
    const viewerRef = useRef(null);
    const [viewer, setViewer] = useState(null);
    const [file, setFile] = useState(null);

    useEffect(() => {
        if (viewerRef.current && !viewer) {
            const initializedViewer = pv.Viewer(viewerRef.current, {
                width: 'auto',
                height: 'auto',
                antialias: true,
                quality: 'medium',
                background: '#000000',
                outline: true,
            });
            setViewer(initializedViewer);

            // Load an initial protein structure (optional)
            pv.io.fetchPdb('http://localhost:3000/uploads/1r6a.pdb', (structure) => {
                initializedViewer.cartoon('protein', structure);
                initializedViewer.autoZoom();
            });
        }
    }, [viewer]);

    useEffect(() => {
        if (file && viewer) {
            const reader = new FileReader();
            reader.onload = (e) => {
                const pdbContent = e.target.result;
                const structure = pv.io.pdb(pdbContent);
                viewer.clear();
                viewer.cartoon('protein', structure);
                viewer.autoZoom();
            };
            reader.readAsText(file);
        }
    }, [file, viewer]);

    const handleFileChange = (event) => {
        const uploadedFile = event.target.files[0];
        if (uploadedFile) {
            setFile(uploadedFile);
        }
    };

    return (
        <div>
            <h1>Protein Viewer</h1>
            <input type="file" accept=".pdb" onChange={handleFileChange} />
            <div
                ref={viewerRef}
                style={{
                    width: '100%',
                    height: '500px',
                    border: '1px solid black',
                    marginTop: '10px',
                }}
            ></div>
        </div>
    );
};

export default ProteinViewer;